.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 800px ;

}

.listafilmes{
    margin: 15px;
}

.listafilmes article{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    margin: 15px;
    padding: 15px;
    border-radius: 5px;

}

.listafilmes strong{
    padding-bottom: 15px ;
    font-size: 20px;   
}

.img{
    max-height: 350px;
    width: 600px;
}

.listafilmes article a{    
    text-decoration: none;
    color: white;
    font-size: 25px;
    background-color: brown;
    width: 100%;
    height: 40px;;
    text-align: center;
    margin-top: -30px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}